@import url(https://fonts.googleapis.com/css2?family=Montserrat&display=swap);
.wrapper {
  width: 340px;
  height: 540px;
  min-height: 540px;
  padding: 10px;
  border-radius: 10px;
  background-color: #485461;
  background-image: linear-gradient(315deg, #485461 0%, #28313b 74%);
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
  display: flex;
  flex-direction: column;
  box-sizing: border-box;
}

.display-section {
  width: 100%;
  margin-bottom: 10px;
  min-height: 190px;  /* Fixed height for screen + history */
}

.screen {
  width: 100%;
  height: 100px;
  padding: 0 10px;
  margin-bottom: 10px;
  background-color: #4357692d;
  border-radius: 10px;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  color: white;
  font-weight: bold;
  box-sizing: border-box;
  font-size: 30px;
}

.screen-content,
.error-message {
  width: 100%;
  text-align: right;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  line-height: 1.2;
}

.screen.error {
  background-color: rgba(255, 99, 71, 0.2);
}

.error-message {
  color: #ff6347;
  font-size: 24px;
}

.history {
  width: 100%;
  height: 80px;
  padding: 10px;
  margin-bottom: 10px;
  background-color: #4357692d;
  border-radius: 10px;
  color: #b5b5b5;
  font-size: 14px;
  box-sizing: border-box;
  overflow-y: auto;
  display: block;
}

.history-entry {
  padding: 2px 0;
  text-align: right;
  line-height: 1.4;
}

/* Custom scrollbar for history */
.history::-webkit-scrollbar {
  width: 6px;
}

.history::-webkit-scrollbar-track {
  background: #4357692d;
  border-radius: 3px;
}

.history::-webkit-scrollbar-thumb {
  background: #888;
  border-radius: 3px;
}

.history::-webkit-scrollbar-thumb:hover {
  background: #555;
}

.responsive-text {
  white-space: nowrap;
  overflow: hidden;
  margin: 0;
}
.buttonBox {
  width: 100%;
  height: 340px;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-template-rows: repeat(5, 1fr);
  grid-gap: 10px;
}

button {
  border: none;
  background-color: rgb(80, 60, 209);
  font-size: 24px;
  color: rgb(255, 255, 255);
  font-weight: bold;
  cursor: pointer;
  border-radius: 10px;
  outline: none;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: background-color 0.2s;
  -webkit-user-select: none;
          user-select: none;
  padding: 0;
}

button:hover {
  background-color: rgb(61, 43, 184);
}

.equals {
  grid-column: 3 / 5;
  background-color: rgb(243, 61, 29);
}

.equals:hover {
  background-color: rgb(228, 39, 15);
}

* {
  margin: 0;
  padding: 0;
  font-family: "Montserrat", sans-serif;
}

body {
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #fbb034;
  background-image: linear-gradient(315deg, #fbb034 0%, #ffdd00 74%);
}

